<template>
  <div id="app">
    <section class="hero is-medium is-danger is-bold">
      <div class="hero-body">
        <div class="container">
          <h1 class="title">
            orz 表情產生器
          </h1>
          <h2 class="subtitle">
            因為我要用這個圖來說話，所以作一個產生器比較方便
          </h2>
          <div class="container">
            <div class="field orz-field">
              <div class="control">
                <input
                  v-model="text"
                  class="input"
                  type="text"
                  placeholder="Text input"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="hero is-danger p-5"></section>
    <div></div>

    <div class="orz-canvas">
      <canvas id="emoji" ref="emoji" class="box"> </canvas>
    </div>
  </div>
</template>
<script>
//
import "bulma";

function clear(canvasDOM) {
  let canvas = canvasDOM;
  let ctx = canvas.getContext("2d");
  ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
}

function generateImg(text, canvasDOM) {
  let canvas = canvasDOM;
  let ctx = canvas.getContext("2d");
  let img = new Image();

  img.onload = () => {
    let imgWidth = img.width;
    let imgHeight = img.height;
    ctx.canvas.width = imgWidth;
    ctx.canvas.height = imgHeight;
    ctx.drawImage(img, 0, 0, imgWidth, imgHeight);
    generateText(text, canvas);
    canvas.toBlob(function(blob) {
      const item = new ClipboardItem({ "image/png": blob });
      navigator.clipboard.write([item]);
    });
  };
  img.src = "emoji.jpg";
}

function generateText(text, canvasDOM) {
  let canvas = canvasDOM;
  let ctx = canvas.getContext("2d");
  let imgWidth = ctx.canvas.width;
  let imgWidthCenter = imgWidth / 2;
  let fontSize = 60;

  // 字體樣式
  ctx.fillStyle = "white";
  ctx.strokeStyle = "black";
  ctx.lineWidth = 10;
  ctx.lineCap = "round";
  ctx.lineJoin = "round";
  ctx.font = `${fontSize}pt Arial`;
  ctx.textAlign = "center";
  // 字體大小計算
  let textWidth = ctx.measureText(text).width;
  let textHeight = ctx.measureText(text).fontBoundingBoxAscent;
  let textTopOffset = 10;
  console.log(textHeight);
  while (textWidth > imgWidth) {
    fontSize--;
    textWidth = ctx.measureText(text).width;
    ctx.font = `${fontSize}pt Arial`;
    textHeight = ctx.measureText(text).fontBoundingBoxAscent;
  }

  // 繪製字體
  ctx.strokeText(text, imgWidthCenter, textHeight + textTopOffset);
  ctx.fillText(text, imgWidthCenter, textHeight + textTopOffset);

  console.log(ctx.canvas.height);
}

export default {
  name: "App",
  data: function() {
    return {
      text: "讓我說話!",
    };
  },
  methods: {
    generate: function() {
      let canvas = this.$refs.emoji;
      let text = this.text;
      console.log(canvas);
      generateImg(text, canvas);
      //   generateText(text, canvas);
    },
  },
  mounted: function() {
    this.$nextTick(function() {
      let text = this.text;
      generateImg(text, this.$refs.emoji);
    });
  },
  watch: {
    text: function() {
      let canvas = this.$refs.emoji;
      let text = this.text;
      console.log(canvas);
      generateImg(text, canvas);
    },
  },
};
</script>

